

@font-face {
    font-family: 'Arial-Rounded';
    src: url('../fonts/Arial-Rounded.eot');
    src: url('../fonts/Arial-Rounded.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Arial-Rounded.woff') format('woff'),
        url('../fonts/Arial-Rounded.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


body{
	margin: 0;
	padding: 0;
	font-family: sans-serif , 'Arial-Rounded';
}

h1,h2,h3,h4,h5,h6{
	margin: 0;
	padding: 0px;
}

p{
	margin: 0;
	padding: 0;
}

ul,ol{
	margin: 0;
	padding: 0;
}

.container{
	max-width: 620px;
	padding: 0 12px ;
	margin: 0 auto;
}