.todos{
	&-inner{
		padding: 10px 20px 0px;
		background-color: white;
		border-top-right-radius: 8px;
		border-top-left-radius: 8px;
		text-align: left;
		margin-top: 12px;
		box-shadow: rgb(0 0 0 / 15%) 0px 10px 20px, rgb(0 0 0 / 10%) 0px 3px 6px;
		animation: 0.1s ease-in-out 0s 1 normal none running;
		&__input{
			border-radius: 4px;
			font-size: 22px;
			padding: 10px 0px;
			box-shadow: none;
			border: none;
			color: rgb(85, 85, 85);
			width: 100%;
			box-sizing: border-box;
			font-weight: bold;
			outline: none;
			opacity: 0.8;
			margin-bottom: 8px;
		}
		&__text{
			color: rgb(85, 85, 85);
			font-weight: bold;
			opacity: .9;
			margin-bottom: 10px;
			display: inline-block;
		}

		&-count{
			display: flex;
			align-items: center;
			margin-bottom: 20px;
			&__input{
				border-radius: 4px;
				background-color: rgb(239, 239, 239);
				font-size: 16px;
				padding: 10px;
				box-shadow: none;
				border: none;
				color: rgb(85, 85, 85);
				width: 75px;
				box-sizing: border-box;
				margin-right: 10px;
				font-weight: bold;
				outline: none;
			}

			&-button{
				align-items: center;
				-webkit-box-pack: center;
				justify-content: center;
				text-align: center;
				border-radius: 4px;
				cursor: pointer;
				opacity: 0.9;
				font-size: 14px;
				padding: 8px 12px;
				display: inline-block;
				min-width: 0px;
				width: 40px;
				background-color: white;
				color: rgb(85, 85, 85);
				border: 1px solid rgb(223, 223, 223);
				box-shadow: rgb(0 0 0 / 20%) 0px 2px 2px;
				margin: 0px 2px;

				&__img{

					&.rov{
						transform: rotate(-180deg);
					}
				}
				
			}
				
				
			
		}

		&-fidbak{
			display: flex;
			align-items: center;
			padding-bottom: 20px;
			&-button{
				outline: none;
				align-items: center;
				justify-content: center;
				text-align: center;
				border-radius: 4px;
				cursor: pointer;
				opacity: 0.9;
				font-size: 14px;
				background: none;
				border: none;
				box-shadow: none;
				color: rgba(0, 0, 0, 0.4);
				font-weight: bold;
				letter-spacing: 0.02em;
				text-decoration: underline;
				display: inline-block;
				&:first-child{
					margin-right: 15px;
				}
			}
		}
	}



	&-footer{
		padding: 14px 20px;
		text-align: right;
		border-bottom-left-radius: 8px;
		border-bottom-right-radius: 8px;
		background-color: rgb(239, 239, 239);
		align-items: center;
		&__cancel{
			align-items: center;
			justify-content: center;
			text-align: center;
			border-radius: 4px;
			cursor: pointer;
			opacity: 0.9;
			font-size: 14px;
			padding: 8px 12px;
			min-width: 70px;
			display: inline-block;
			margin-right: 14px;
			background: none;
			border: none;
			color: rgb(136, 136, 136);
			font-weight: bold;
			box-shadow: none;
		
		}

		&__save{
			text-align: center;
			border-radius: 4px;
			cursor: pointer;
			box-shadow: rgb(0 0 0 / 20%) 0px 2px 2px;
			color: white;
			opacity: 0.9;
			font-size: 14px;
			padding: 8px 12px;
			min-width: 70px;
			border: 2px solid rgb(171, 171, 171);
			pointer-events: none;
			display: inline-block;
			background-color: rgb(171, 171, 171);
		}
	}
}