.app-page{

	width: 100%;
	height: 100%;
    min-height: 100vh;
    width: 100%;
    color: white;
    transition: background-color 0.5s ease-in-out 0s;
    padding-bottom: 12px;
    box-sizing: border-box;
    padding-top: env(safe-area-inset-top);
	&.pomo{
		background-color: rgb(217, 85, 80);
		.app-page-header{
			border-bottom: 1px solid #C24B49;
		}
		#timer-start{
			color: #C24B49 !important;
		}

	}
	&.short{
		background-color: rgb(76, 145, 149);
		.app-page-header{
			border-bottom: 1px solid #458186;
		}

		#timer-start{
			color: #458186 !important;
		}
	}

	&.long{
		background-color: rgb(69, 124, 163);
		.app-page-header{
			border-bottom: 1px solid #3E6F92;
		}

		#timer-start{
			color: #3E6F92 !important;
		}
	}
	&-header{
		padding: 12px 12px !important;
		max-width: 620px;
		margin: auto;
		display: flex;
		align-items: center;
		justify-content: space-between;
		&-logo{
			display: flex;
			align-items: center;
			text-decoration: none;
			color: white;
			font-size: 20px;
			font-weight: bold;
			&__img{
				margin-right: 4px;
			}
		}

		&-right{
			display: flex;
			align-items: center;

			&__button{
				display: flex;
				align-items: center;
				justify-content: center;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				border-radius: 4px;
				opacity: 0.8;
				background: none rgba(255, 255, 255, 0.2);
				box-shadow: none;
				margin-left: 10px;
				font-size: 13px;
				padding: 8px 12px;
				min-width: 70px;
				border: none;
				color: white !important;
				text-decoration: none;
				&__img{
					margin-right: 4px;
				}
				&:hover{
					opacity: 1;
				}
			}
		}
	}

	&-main{
		max-width: 480px;
    	margin: auto;
		margin-top: 40px !important;
		background-color: rgba(255, 255, 255, 0.1);
		padding: 20px 0px 30px;
		border-radius: 6px;
		box-sizing: border-box;
		text-align: center;
		&-top{
			display: flex;
			align-items: center;
			justify-content: center;

			&__button{
				border: none;
				color: white;
				margin: 0px;
				border-radius: 4px;
				font-size: 16px;
				padding: 2px 12px;
				height: 28px;
				cursor: pointer;
				background: none;
				box-shadow: none;
				font-weight: 300;

				&.active{
					border: none;
					margin: 0px;
					border-radius: 4px;

					font-size: 16px;
					padding: 2px 12px;
					height: 28px;
					cursor: pointer;
					background: none rgba(0, 0, 0, 0.15);
					box-shadow: none;
					color: white;
					opacity: 1;
					font-weight: bold;

					
				}
			}

			
		}

		&-timer{
			
			display: none;
			&.active{
				display: block;
			}
			
		}
		&__time{
			font-size: 120px;
			font-weight: bold;
			margin-top: 20px;
		}
		&-start{
			position: relative;
			&__button{
				cursor: pointer;
				border: none;
				margin: 20px 0px 0px;
				padding: 0px 12px;
				border-radius: 4px;
				box-shadow: rgb(235 235 235) 0px 6px 0px;
				font-size: 22px;
				height: 55px;
				color: rgb(217, 85, 80);
				font-weight: bold;
				width: 200px;
				background-color: white;
				transition: color 0.5s ease-in-out 0s;

				&.start{
					cursor: pointer;
					border: none;
					margin: 20px 0px 0px;
					padding: 0px 12px;
					border-radius: 4px;
					font-size: 22px;
					height: 55px;
					color: rgb(217, 85, 80);
					font-weight: bold;
					width: 200px;
					background-color: white;
					transform: translateY(6px);
					box-shadow: none;
					transition: color 0.5s ease-in-out 0s
				}
			}

			&-next{
				width: calc((100% - 200px) / 2 + 5%);
				position: absolute;
				right: 0px;
				bottom: -6px;
				height: 54px;
				display: flex;
				-webkit-box-align: center;
				align-items: center;
				-webkit-box-pack: center;
				justify-content: center;
				pointer-events: none;
				display: none;
				z-index: 10;
				&-button{
					cursor: pointer;
					user-select: none;
					background: none;
					border: none;
					width: 28px;
					height: 28px;
					align-items: center;
					justify-content: center;
					bottom: 9px;
					right: -50px;
					pointer-events: none;
					transition: all 0.2s ease-out 0s;
				}

				&.active{
					display: flex;
				}
			}
		}
	}


	.task{
		text-align: center;
		max-width: 480px;
		margin: 20px auto ;
		&-top{
			display: flex;
			flex-direction: column;
			margin-bottom: 20px;

			&__count{
				font-size: 16px;
				opacity: 0.6;
				margin-bottom: 4px;
			}

			&__title{
				font-size: 18px;
				font-weight: 100;
				color: white;
				overflow-wrap: break-word;
			}
		}

		&-see{
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 2px solid rgba(255, 255, 255, 0.6);
			padding-bottom: 14px;
			&__text{
				font-size: 18px;
				font-weight: bold;
				color: #fff;
			}

			&-right{

				&-button{
					border-radius: 4px;
					cursor: pointer;
					opacity: 0.9;
					background: none rgba(255, 255, 255, 0.2);
					box-shadow: none;
					margin-left: 10px;
					font-size: 13px;
					padding: 8px;
					min-width: auto;
					border: none;
					color: white !important;
				}
			}
		}

		&-add{
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 60px;
			background-color: rgba(0, 0, 0, 0.1);
			border-radius: 8px;
			cursor: pointer;
			opacity: 0.8;
			margin-top: 12px;
			border: 2px dashed rgba(255, 255, 255, 0.4);
			&__img{
				width: 18px;
				margin-right: 8px;
				opacity: 0.8;
			}
			&__text{
				opacity: 0.8;
				font-weight: bold;
			}

			&:hover{
				opacity: 1;
				.task-add__img{
					opacity: 1;
				}
				.task-add__text{
					opacity: 1;
				}
			}
		}

		.todos-wrapper{
			width: 100%;
			overflow: hidden;
			height: 0;
			transition: all ease-in-out 0.3s;
			&.active{
				height: 290px;
			}
		}
	}
}


