.login{
	background-color: rgb(217, 85, 80);
	width: 100%;
	height: 100%;
	padding: 19px 0 2px 0;
	&-classname{

		min-height: calc(100vh - 5px);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 0px 18px;
		width: 100%;
		box-sizing: border-box;
		.top{
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
		}
		&-logo{
			
			&__img{
				width: 280px !important;
			}
		}
		&__title{
			text-align: center;
			font-size: 18px;
			font-weight: 600;
			color: white;
			opacity: 0.8;
			letter-spacing: 0.05em;
			margin-top: 18px;
			margin-bottom: 38px;
		}


		&-card{
			width: 350px;
			background-color: white;
			border-radius: 8px;
			box-shadow: rgb(196 196 196) 0px 1px 0px;
			padding: 20px 18px 32px;
			box-sizing: border-box;
			overflow: hidden;
			&-google{
				
				&-button{

					width: 100%;
					display: flex;
					// -webkit-box-align: center;
					align-items: center;
					// -webkit-box-pack: center;
					justify-content: center;
					text-align: center;
					border-radius: 4px;
					cursor: pointer;
					box-shadow: rgb(0 0 0 / 20%) 0px 2px 2px;
					opacity: 0.9;
					font-size: 14px;
					padding: 12px;
					min-width: 70px;
					background-color: white;
					color: rgb(119, 119, 119);
					font-weight: bold;
					letter-spacing: 0.02em;
					width: 100%;
					border: 1px solid rgb(238, 238, 238);
				
					&:active{
						box-shadow: none;
					}


					&__img{
						width: 18px;
					    margin-right: 8px;
					}

				}

			}

			&-line{
				display: flex;
				justify-content: center;
				align-items: center;
				margin: 14px 0px;
				width: 100%;
				position: relative;
				overflow: hidden;
				&::before{
					content: '';
					position: absolute;
					width: 42%;
					height: 1px;
					background-color: rgb(235, 235, 235);
					left: 0;
					bottom: 5x;
				}
				&::after{
					content: '';
					position: absolute;
					width: 42%;
					height: 1px;
					background-color: rgb(235, 235, 235);
					right: 0;
					bottom: 5x;
				}
				&__text{
					color: rgb(196, 196, 196);
				}
			}

			&-form{
				display: flex;
				flex-direction: column;
				flex-wrap: wrap;
				&-inner{
					display: flex;
					flex-direction: column;
					margin-bottom: 24px;
					&__label{
						color: rgb(196, 196, 196);
						font-weight: 700;
						text-transform: uppercase;
						font-size: 12px;
						margin-top: 20px;
						margin-bottom: 8px;
					}
					&__input{
						border-radius: 4px;
						background-color: rgb(239, 239, 239);
						font-size: 16px;
						padding: 12px 10px;
						box-shadow: none;
						border: none;
						color: rgb(85, 85, 85);
						width: 100%;
						box-sizing: border-box;
						outline: none;

						&::placeholder{
							background-color: rgb(239, 239, 239);
							color: rgb(85, 85, 85);
							opacity: 0.4  ;
						}
					}
				}
			}

			&-email{
				
				margin-bottom: 20px;
				&__button{
					display: flex;
					-webkit-box-align: center;
					align-items: center;
					
					justify-content: center;
					text-align: center;
					border-radius: 4px;
					cursor: pointer;
					box-shadow: rgb(0 0 0 / 20%) 0px 2px 2px;
					color: white;
					opacity: 0.9;
					font-size: 14px;
					padding: 12px;
					min-width: 70px;
					background-color: rgb(34, 34, 34);
					border: 2px solid rgb(34, 34, 34);
					width: 100%;

				}
			}

			&-forgot{
				display: flex;
				align-items: center;
				justify-content: center;
				&__button{
					display: flex;
					
					align-items: center;
					
					justify-content: center;
					text-align: center;
					opacity: 0.9;
					font-size: 14px;
					background: none;
					border: none;
					box-shadow: none;
					color: rgba(0, 0, 0, 0.4);
					font-weight: bold;
					letter-spacing: 0.02em;
					text-decoration: underline;
				}
			}

		}

		&-bottom{
			margin-top: 24px;
			

			&__text{
				font-size: 16px;
				color: rgba(255, 255, 255, 0.8);
				text-align: center;
				display: flex;
				-webkit-box-pack: center;
				justify-content: center;
				flex-direction: column;
			}

			&__link{
				font-size: 16px;
				color: rgba(255, 255, 255, 0.8);
				text-align: center;
				display: flex;
				-webkit-box-pack: center;
				justify-content: center;
				flex-direction: column;
				margin-top: 8px;
			}
		}
	}
	
}