.home-inner{

	&__title{
		display: block;
		margin-top: 50px;
		margin-bottom: 20px;
		color: hsl(357, 50%, 22%);
		line-height: 1.4em;
		font-size: 34px;
	}

	&-part{

		&__title{
			color: hsl(357, 50%, 22%);
			margin-top: 1.8rem;
			margin-bottom: 15px;
			display: inline-block;
			
			font-size: 1.5em;
			margin-block-start: 0.83em;
			margin-block-end: 0.83em;
			margin-inline-start: 0px;
			margin-inline-end: 0px;
			font-weight: bold;
			position: relative;
			&::after{
				content: '';
				display: block;
				width: 24px;
				padding-top: 8px;
				border-bottom: 4px solid #f05b56;
				opacity: 0.6;

			}
		}

		&__text{
			color: hsl(357, 12%, 42%);
			font-size: 18px;
			line-height: 1.4em;
			letter-spacing: .5px;
			position: relative;
			width: 100%;
			display: block;
			margin-block-start: 1em;
			margin-block-end: 1em;
			margin-inline-start: 0px;
			margin-inline-end: 0px;
		}

		&-ol{
			padding-left: 20px;
			color: hsl(357, 12%, 42%);
			li{
				line-height: 1.6rem;
				color: hsl(357, 12%, 42%);
				margin: 12px 0px;
			}
		}

		&__list{
			list-style: none;
			li{
				line-height: 1.6rem;
				color: hsl(357, 12%, 42%);
				margin: 12px 0px;
				font-size: 18px;
				position: relative;
				display: inline-block;
				&::before{
					color: hsl(357, 74%, 67%);
					content: '\2022';
					font-weight: bold;
					display: inline-block;
					width: 1em;
					margin-left: -1em;
				
				}
			}
		}

		&-bottom{

			&-li{
				line-height: 1.6rem;
				margin: 12px 0px;
				color: hsl(357, 12%, 42%);
				font-size: 18px;
				&__link{
					
					text-decoration: none !important;
					color: rgb(217 85 80);
					font-weight: bold;
					font-size: 18px;
				}
			}
		}
	}

	
}