.privacy{

	&-header{

		border-bottom: 1px solid #dfdfdf;

		&__img{
			width: 26px !important;
			height: 26px !important;
		}
		&__link{
			color: rgb(217 85 80);
			font-size: 28px;
			text-decoration: none !important;
			font-weight: bold;
			padding: 16px 0;
			display: inline-block;

		}

		
		

		&-part{

			&__title{
				margin-top: 40px;
				margin-bottom: 20px;
				color: hsl(357, 50%, 22%);
				padding-bottom: 4px;
				line-height: 1.4em;
				&.top{
					font-size: 34px;
				}
				&.h2{
					font-size: 1.5em;
					margin-block-start: 0.83em;
					margin-block-end: 0.83em;
					margin-inline-start: 0px;
					margin-inline-end: 0px;
					font-weight: bold;
				}
				&.h3{
					display: block;
					font-size: 1.17em;
					margin-block-start: 1em;
					margin-block-end: 1em;
					margin-inline-start: 0px;
					margin-inline-end: 0px;
					font-weight: bold;
				}
				&.h4{
					display: block;
					margin-block-start: 1.33em;
					margin-block-end: 1.33em;
					margin-inline-start: 0px;
					margin-inline-end: 0px;
					font-weight: bold;
				}
			}

			&__text{
				font-size: 18px;
				line-height: 1.6em;
				color: hsl(357, 12%, 42%);
				display: block;
				margin-block-start: 1em;
				margin-block-end: 1em;
				margin-inline-start: 0px;
				margin-inline-end: 0px;

				&-link{
					text-decoration: none !important;
					color: rgb(217 85 80);
					font-weight: bold;
					font-size: 18px;
					line-height: 1.6em;
				}
			}

			&-ul1{
				font-size: 18px;
				list-style: none !important;
				-webkit-padding-start: 24px;
				&__list{

					line-height: 1.6rem;
					color: hsl(357, 12%, 42%);
					margin: 12px 0px;
				}
			}
			&-ul2{
				font-size: 18px;
				list-style: none;
				-webkit-padding-start: 24px;
				&__list{
					line-height: 1.6rem;
					color: hsl(357, 12%, 42%);
					margin: 12px 0px;
				}
			}


			
		}
	}
}
