.footer{
	margin-top: 40px;
    border-top: 2px solid #efefef;
    text-align: center;
    padding-top: 30px;
    color: hsl(357, 12%, 42%);
    padding-bottom: 20px;

	&-sitemap{
		margin-bottom: 15px;
		&__link{
			color: hsl(357, 12%, 42%);
			font-size: 14px;
			margin: 0px 4px;
			text-decoration: none;
			font-weight: bold;
		}
	}

	&-snslinks{
		a{
			img{
				
				background-color: #999;
				width: 28px;
				height: 28px;
				border-radius: 100px;
				padding: 6px;
				margin: 0px 6px;
			}
		}

		&-me{
			opacity: 0.8;
			margin-bottom: 16px;
			a{
				text-decoration: none !important;
				color: rgb(217 85 80);
				font-weight: bold;
			}
		}

		&-allrights{
			font-size: 12px;
			color: hsl(357, 8%, 60%);
		}
	}
}